import React, { useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Link } from "react-router-dom";
import {
  deleteItemIncartAsync,
  selectCartProduct,
  updateCartAsync,
  selectCartStatus,
} from "./CartSlice";
import { useAlert } from "react-alert";
import { Grid } from "react-loader-spinner";
import Modal from "../Common/Modal";

export default function Cart() {
  const items = useSelector(selectCartProduct);
  // console.log(items);
  const [open, setOpen] = useState(true);
  const totalAmount = items ? items.reduce((total , item) => item.quantity * (item.product.discountedPrice) + total , 0 ) : 0;
  const totalItems = items ? items.reduce((count, item) => item.quantity + count, 0) : 0;
  const alert = useAlert();
  const dispatch = useDispatch();
  const status = useSelector(selectCartStatus);
  const [openModal, setOpenModal] = useState(null);

  const handleCartUpdate = (e, item) => {
    alert.info("Cart Updated");
    dispatch(updateCartAsync({cartId : items[0]._id , id : item.id, quantity: +e.target.value }));
  };

  const handleDeleteItem = (itemId) => {
    // console.log("item id =" , itemId);
    dispatch(deleteItemIncartAsync(itemId));
    alert.info("Item Deleted");
    setOpenModal(null);
  };
  return (
    <>
      {/* {items.length == 0 &&   <Navigate to={"/"}></Navigate>} */}
      <div className="mx-auto max-w-7xl px-4 mt-10 sm:px-6 lg:px-8 bg-white">
        <h1 className="text-3xl font-bold tracking-tight text-gray-900">
          Shopping Cart
        </h1>
        <div className=" border-gray-200 px-4 py-6 sm:px-6">
          <div className="flow-root">
            <ul role="list" className="-my-6 divide-y divide-gray-200">
              {status === "loading" ? (
                <Grid
                  height="80"
                  width="80"
                  color="rgb(79, 70, 229) "
                  ariaLabel="grid-loading"
                  radius="12.5"
                  wrapperStyle={{}}
                  wrapperClass=""
                  visible={true}
                />
              ) : null}
              
              {items.map((item) => (
                <li key={item.product.id} className="flex py-6">
                  <div className="h-24 w-24 flex-shrink-0 overflow-hidden rounded-md border border-gray-200">
                    <img
                      src={item.product.images[0]}
                      className="h-full w-full object-cover object-center"
                    />
                  </div>

                  <div className="ml-4 flex flex-1 flex-col">
                    <div>
                      <div className="flex justify-between text-base font-medium text-gray-900">
                        <h3>
                          <Link to={"/product-details/" + item.product.id}>
                            {item.product.title}
                          </Link>
                        </h3>
                        <p className="ml-4">$ {(item.product.discountedPrice)}</p>
                      </div>
                      <p className="mt-1 text-sm text-gray-500">
                        {item.product.category }
                      </p>
                    </div>
                    <div className="flex flex-1 items-end justify-between text-sm">
                      <div className="text-gray-500 ">
                        <label
                          htmlFor="quantity"
                          className="inline mr-5 text-sm font-medium leading-6 text-gray-900"
                        >
                          Qty
                        </label>
                        <select
                          onChange={(e) => handleCartUpdate(e, item.product)}
                          className=""
                          value={item.quantity}
                        >
                          <option value={"1"}>1</option>
                          <option value={"2"}>2</option>
                          <option value={"3"}>3</option>
                          <option value={"4"}>4</option>
                          <option value={"5"}>5</option>
                        </select>
                      </div>

                      <div className="flex">
                        <Modal
                          title={item.product.title}
                          showModal={openModal === item.product.id}
                          dangerAction={() => handleDeleteItem(item.product.id)}
                          cancelAction={()=>setOpenModal(null)}
                        ></Modal>
                        <button
                          onClick={() => {
                            setOpenModal(item.product.id);
                          }}
                          type="button"
                          className="font-medium text-indigo-600 hover:text-indigo-500"
                        >
                          Remove
                        </button>
                      </div>
                    </div>
                  </div>
                </li>
              ))}
            </ul>
          </div>
        </div>
        <div className="border-t border-gray-200 px-4 py-6 sm:px-6">
          <div className="flex justify-between my-2 text-base font-medium text-gray-900">
            <p>Subtotal</p>
            <p>${totalAmount}</p>
          </div>
          <div className="flex justify-between my-2 text-base font-medium text-gray-900">
            <p>Total items in Cart : </p>
            <p>{totalItems} items</p>
          </div>
          <p className="mt-0.5 text-sm text-gray-500">
            Shipping and taxes calculated at checkout.
          </p>
          <div className="mt-6">
            <Link
              to={"/checkout"}
              className="flex items-center justify-center rounded-md border border-transparent bg-indigo-600 px-6 py-3 text-base font-medium text-white shadow-sm hover:bg-indigo-700"
            >
              Checkout
            </Link>
          </div>
          <div className="mt-6 flex justify-center text-center text-sm text-gray-500">
            <p>
              or{" "}
              <Link
                to={"/"}
                type="button"
                onClick={() => setOpen(false)}
                className="font-medium text-indigo-600 hover:text-indigo-500"
              >
                Continue Shopping
                <span aria-hidden="true"> &rarr;</span>
              </Link>
            </p>
          </div>
        </div>
      </div>
    </>
  );
}
